import React from 'react';
import Drawer from '@material-ui/core/Drawer';
import MenuList from './MenuList';

const DrawerMenu = ({
  show,
  current,
  onClose,
  location,
  history
}) => {
  return React.createElement(Drawer, {
    open: show,
    onClose: onClose
  }, React.createElement(MenuList, {
    location: location,
    history: history
  }));
};

export default DrawerMenu;